<template>
  <div class="my-head">
    <nav class="navbar navbar-default navbar-fixed-top" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <button type="button" class="navbar-toggle" data-toggle="collapse"
                  data-target="#example-navbar-collapse">
            <span class="sr-only">切换导航</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <a class="navbar-brand" href="#" :style="{padding:'0 10px'}">
            <img src="../assets/head.jpg" class="img-circle" :style="{height:'50px'}">
          </a>
        </div>
        <div class="collapse navbar-collapse" id="example-navbar-collapse">
          <ul class="nav navbar-nav navbar-right">
            <!--通过数据来设置导航栏-->
            <li v-for="item of navList" :class="{active : item.flag}" @click="cNavClick(item)"><a href="#">{{ item.name
              }}</a></li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
  export default {
    name: "my-head",
    data() {
      return {
        cNavFlag: false,
        navList: [
          {name: '个人信息', flag: true},
          {name: '技能展示', flag: false},
          {name: '作品展示', flag: false},
          {name: '工作经历', flag: false},
          {name: '联系我', flag: false}
        ]
      }
    },
    methods: {
      cNavClick(item) {
        this.navList.forEach(function (obj) {
          obj.flag = false;
        })
        item.flag = !item.flag
        
      }
    }
  }
</script>

<style scoped>

</style>



